import React from 'react';
import { CarouselProvider, Slider, Slide, DotGroup, Image, ImageWithZoom} from 'pure-react-carousel';

import 'pure-react-carousel/dist/react-carousel.es.css';


/*carousel is responsive by default, will stretch to fill in the width of it's parent container.
The only required properties are the orientation, naturalSlideWidth, and naturalSlideHeight.
*/
 const Carousel = ({images}) => (
  <CarouselProvider
    visibleSlides={1}
    totalSlides={3}
    naturalSlideWidth={310}
    naturalSlideHeight={550}
    isPlaying={true}
    interval={8000}
  >
    <div>
      <Slider>
        {/* slides require a sequentially numbered index prop starting at zero. 
        Thinking of adding an "on click" here to navigate to github*/}
        <Slide index={0}>
           <Image src={images[0]}  />
        </Slide>
        <Slide index={1}>
           <ImageWithZoom src={images[1]}/>
        </Slide>
        <Slide index={2}>
           <ImageWithZoom src= {images[2]}/>
        </Slide>
      </Slider>
     </div>
    <DotGroup class="dotGroup"/>
  </CarouselProvider>
);

export default Carousel;